/** @format */

.icon_logo {
	width: 42px;
	height: 17px;
	margin-bottom: 40px;
}

.main_text {
	margin: 0;
	font-family: Gilroy;
	font-size: 32px;
	font-weight: 700;
	line-height: 32px;
	letter-spacing: 2%;
	color: rgb(249, 249, 249);
}

.main_text_ext {
	color: rgb(227, 227, 227, 0.5);
}

.button_hide_password {
	width: 18px;
	height: 18px;
	background-color: transparent;
	border: none;
}

.icon_button_hide_password {
	width: 18px;
	height: 18px;
	fill: transparent;
	stroke: rgb(249, 249, 249);
}
